body #load {
    display: block;
    height: 100%;
    overflow: hidden;
    position: fixed;
    width: 100%;
    z-index: 9901;
}
.load-wrap {
    width: 100%;
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    text-align: center;
}
.load-wrap > svg {
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
}
#load {
    background-color: #d20a11;
}
.uil-default rect:not(.bk) {
    fill: #ffffff;
}
.uil-ring > path {
    fill: #ffffff;
}
.ring-loader .circle {
    fill: #ffffff;
}
.ring-loader .moving-circle {
    fill: #ffffff;
}
.uil-hourglass .glass {
    stroke: #ffffff;
}
.uil-hourglass .sand {
    fill: #ffffff;
}
